p {
    margin: 0;
}

.content{
    display: flex;
    flex-direction: column;
    width: 300px;
    border-radius: 8px;
    box-shadow: 5px 8px 5px #888888;
    box-shadow: 5px 10px 10px #444444;
    background-color: linear-gradient(180deg, rgba(0, 8, 24, 0.815), y rgba(0, 22, 65, 0.096));
    z-index: 2;
}

.bcg {
    display: none;
    position: absolute;
    width: 300px;
    z-index: 1;
    
}

.subContent {
    display: flex;
    justify-content: space-evenly;
    padding-bottom: 20px;
    z-index: 3;
}

.subDiv {
    display: flex;
    flex-direction: row;
    align-self: center;
    margin-bottom: 0;
    gap: 10px;
    z-index: 4;
}

.subSubDiv {
    width: 55px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.507);
    padding: 4px 8px;
    border-radius: 4px;
    z-index: 5
}

.subSubDiv > p {
    color: aliceblue;
    padding: 5px 0;
    
}

.title {
    padding: 0 0 0 30px;
    margin-bottom: 0;
    color: aliceblue;
    z-index: 7;
}

.img {
    border-radius: 50%;
    /* box-shadow: 10px 10px 8px #57cfff88; */
    /* background: radial-gradient(circle at 100%, #333, #333 50%, #eee 75%, #333 75%); */
    background-color: 0.5;
    background: radial-gradient(rgba(255, 255, 255, 0.479) 20%, rgba(255, 255, 255, 0) 150%)
}

.btn {
    width: min-content;
    align-self: flex-end;
    margin: 8px 8px 0 0;
    padding: 0 7px;
    border: none;
    border-radius: 50%;
    background-color: rgb(207, 124, 124);
    color: rgb(143, 38, 38);
    box-shadow: 2px 3px 3px #46454573;
    z-index: 7;
    
}
